import Vue from 'vue'
import App from './App'
import vuetify from './plugins/vuetify';
import router from './plugins/vuerouter';
import store from './store';
import './registerServiceWorker'

Vue.config.productionTip = false

window.Event = new Vue();

new Vue({
    render: h => h(App),
    router,
    store,
    vuetify
}).$mount('#app')
