<template>
    <v-container fluid :style="{color: $store.getters.current.textColor}">
        <v-container fluid class="mb-5"
                     :style="{backgroundColor: $store.getters.current.secondary + $store.getters.alpha}">
            <h1>Settings</h1>
        </v-container>
        <v-container fluid :style="{backgroundColor: $store.getters.current.secondary + $store.getters.alpha}">
        <v-select
                label="Theme"
                :items="items"
                item-value="val"
                item-text="disp"
                @input="setTheme"
                :value="$store.state.current"
                :color="$store.getters.current.textColor"
                :dark="$store.getters.current.isDark"
        >
        </v-select>
        <v-switch label="Background Image"
                  v-model="bg"
                  @change="$store.commit('bg', bg); saveChanges()"
                  :dark="$store.getters.current.isDark"
        ></v-switch>
        <div :hidden="!bg">
            <v-text-field
                    label="Image URL"
                    v-model="bgimg"
                    :dark="$store.getters.current.isDark"
                    @change="$store.commit('setBg', bgimg); saveChanges()"
            >
            </v-text-field>
            <h6>;-;</h6>
        </div>
        </v-container>
    </v-container>
</template>

<script>
    import store from "../store/index";

    if (localStorage.theme)
        store.commit('setTheme', localStorage.theme);
    document.body.style.backgroundColor = store.getters.current.backgroundColor;
    if (localStorage.hasbg)
        store.commit('bg', localStorage.hasbg)
    if (localStorage.bg)
        store.commit('setBg', localStorage.bg)
    export default {
        name: "Options",
        methods: {
            setTheme(newt) {
                store.state.current = newt;
                localStorage.theme = newt;
                store.commit('setTheme', newt);
                document.body.style.backgroundColor = store.getters.current.backgroundColor;
            },
            saveChanges(){
                localStorage.hasbg = store.getters.hasBgImage;
                localStorage.bg = store.getters.bgImg;
            }
        },
        data() {
            return {
                items: [{
                    disp: 'Light',
                    val: 'light'
                }, {
                    disp: 'Dark',
                    val: 'dark'
                }, {
                    disp: 'Midnight',
                    val: 'midnight'
                }],
                bg: store.getters.hasBgImage,
                bgimg: store.getters.bgImg,
            }
        },

    }
</script>

